// после того, как передали аргументы в функции openModal и closeModal, нам надо не забыть передать селектор в качестве аргумента во все случаи, когда мы вызываем функцию или используем openModal и closeModal

function openModal(modalSelector, modalTimerId) { // сюда также передаем + таймер
    const modal = document.querySelector(modalSelector); // повторяем код вычленения элемента со страницы по селектору, чтобы тут использовать переменную modal

    modal.classList.add('show'); 
    modal.classList.remove('hide'); 
    document.body.style.overflow = 'hidden';

    console.log(modalTimerId); // посмотрим, что будет с таймером при открытияя модального окна
    if (modalTimerId) { // если этот интервал вообще существует, только в таком случае мы запускаем код
    clearInterval(modalTimerId);
    }
}

function closeModal(modalSelector) { // сюда также передаем аргумент
    const modal = document.querySelector(modalSelector); // повторяем код, чтобы тут использовать переменную modal

    modal.classList.add('hide'); 
    modal.classList.remove('show'); 
    document.body.style.overflow = ''; 
}


function modal(triggerSelector, modalSelector, modalTimerId) { // добавляем параметры, именно их я буду использовать, когда буду вызывать функцию modal.  modalTimerId передаем везде, где вызывается ыункция openModal внутри функции modal.

    // МОДАЛЬНОЕ ОКНО 


    const modalTrigger = document.querySelectorAll(triggerSelector); // вырезаю атрибут, чтобы вставить его в script.js в запуске функции, а сюда вставляю переданные аргументы
    const modal = document.querySelector(modalSelector); 
    


    modalTrigger.forEach(item => {
        item.addEventListener('click', () => openModal(modalSelector, modalTimerId)); // подставляем аргумент. Но тут есть одна загвоздка: мы помним, что в обработчике события при использовании коллбек функции, мы не должны ее вызывать, мы должны ее только объявить. Она сама вызовется, когда будет произведен клик. Сейчас мы нарушаем это правило. Для того, Чтобы обойти это ограничение, создается стрелочная функция, которая оборачивает вызывающуюся функцию. Теперь и она будет выпоняться после клика.
    });


    modal.addEventListener('click', (e) => {
        if (e.target === modal || e.target.getAttribute('data-close') == '') { 
            closeModal(modalSelector); // подставляем селектор
        }
    });



    // const modalTimerId = setTimeout(openModal, 50000); // ПЕРЕМЕСТИЛИ В ГЛАВНЫЙ ФАЙЛ

    window.addEventListener('scroll', showModalByScroll);

    function showModalByScroll() { 
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight) { 
            openModal(modalSelector, modalTimerId); // подставляем селектор
            window.removeEventListener('scroll', showModalByScroll);
        }
    }
}

// module.exports = modal; (метод CommoJS)
export default modal;
export {closeModal};
export {openModal};