import {openModal, closeModal} from './modal'; // импортируем сюда функции, теперь необходимо в эти функции, которые мы использовали на странице,  передать аргументы, тк теперь они вызываются с аргументами
import {postData} from '../services/services';


function forms(formSelector, modalTimerId) { // передаю аргументы
    // ОБЩЕНИЕ С СЕРВЕРОМ

   

    const forms = document.querySelectorAll(formSelector); // подставляю аргумент
    const message = { 
        loading: 'img/spinner.svg',
        success: 'Спасибо! Скоро мы с вами свяжемся',
        failure: 'Что-то пошло не так...'
    };

    forms.forEach(item => {
        bindPostData(item);
    }) 

    // функция postData работает с сервисами, обычно их так и называют - Сервисы, их обычно выносят в отдельную папку/файл, тк они могут использоваться где угодно, особенно если их становится все больше, поэтому так и поступим. Внутри папки js создадим папку Services, а внутри нее файл services.js. Вырезаем отсюда postData и переставляем туда.
    
    // const postData = async (url, data) => {  
    //     const res = await fetch(url, { 
    //         method: 'POST',
    //         headers: { 
    //             'Content-type': 'application/json' 
    //         },
    //         body: data 
    //     }); 

    //     return await res.json(); 
    // };


    function bindPostData(form) {
        form.addEventListener('submit', (e) => {    
            e.preventDefault(); 

            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading; 
            statusMessage.style.cssText = `
            display: block;
            margin: 0 auto;
            `; 
            

            form.insertAdjacentElement('afterend', statusMessage);
           
           

            const formData = new FormData(form); 

            const json = JSON.stringify(Object.fromEntries(formData.entries())); 
            postData('http://localhost:3000/requests', json)
            .then(data => data.text())
            .then(data => {
                console.log(data);  
                showThanksModal(message.success);  
                statusMessage.remove(); 
            }).catch(() => {
                showThanksModal(message.failure);
            }).finally(() => {
                form.reset();
            })

            
        });

    }


    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog');
      
        prevModalDialog.classList.add('hide');
    
        openModal('.modal', modalTimerId); // подставляем аргументы

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog'); 
        thanksModal.innerHTML = `
        <div class="modal__content">
            <div data-close class="modal__close">&times;</div>
            <div class="modal__title">${message}</div>
        </div>
        `;

       
        document.querySelector('.modal').append(thanksModal);

        
        setTimeout(() => {
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal('.modal'); // передаем аргумент
        }, 4000);

    }
}

// module.exports = forms; (метод CommoJS)
export default forms;