// здесь также выносим селекторы и передаем их в виде аргументов

function tabs(tabsSelector, tabsContentSelector, tabsParentSelector, activeClass) {
    // ТАБЫ

     // подставляем аргументы вместо самого селектора
    const tabs = document.querySelectorAll(tabsSelector), 
          tabsContent = document.querySelectorAll(tabsContentSelector), 
          tabsParent = document.querySelector(tabsParentSelector); 
     
    function hideTabContent() { 
        tabsContent.forEach(item => {

            item.classList.add('hide');
            item.classList.remove('show', 'fade');
        });

        tabs.forEach(item => {
            item.classList.remove(activeClass);  // подставляем аргумент вместо самого селектора
        });
    }  

    function showTabContent(i = 0) { 
    
        tabsContent[i].classList.add('show', 'fade'); 
        tabsContent[i].classList.remove('hide');
        tabs[i].classList.add(activeClass);  // подставляем аргумент вместо самого селектора
    }

    hideTabContent();
    showTabContent(); 

    tabsParent.addEventListener('click', (event) => {
        const target = event.target; 

        if (target && target.classList.contains(tabsSelector.slice(1))) { // тк здесь передается селектор уже без точки, то нам надо эту точку вырезать
            tabs.forEach((item, i) => { 
                if (target == item) {
                    hideTabContent();
                    showTabContent(i); 
                }
            });
        }
    });
}

// module.exports = tabs; (метод CommoJS)
export default tabs; 