    slider({ // передаем объект, который содержит настройки, чтобы по подобию создавать другие слайдеры. Свойства создаем в любом порядке
        container: '.offer__slider',
        nextArrow: '.offer__slider-next',
        prevArrow: '.offer__slider-prev',
        slide: '.offer__slide',
        totalCounter: '#total',
        currentCounter: '#current',
        wrapperSlider: '.offer__slider-wrapper',
        trackSlider: '.offer__slider-inner'
    }); 

    // теперь собираем эту структуру: заходим в терминал и вводим npx webpack, появятся файлы bundle, и теперь можем тестировать. Здесь есть интересный лайфхак: когда мы запускаем сборщик в таком режиме, он следит за нашими проектами, то есть если я что-то изменю в каком-то файле и сохраню, он снова сам сделает сборку (после запуска сборщика он не выводит путь к файлу в терминале, как обычно, строчка пустая, так можно проверить, что он следит). Здесь вопрос: мне необходимо запустить json-server, чтобы я мог делать запросы к серверу и получать данные, но при этом терминал для этого я использовать уже не могу, он занят сборщиком. В VS Code можно создать новый терминал, нажав на плюсик (во вкладках можно переключать). Там запускаем json-server и все правильно отрабатываем.

    // Теперь чтобы все правильно работало, в index.html подключаем другой файл (вместо script - js/bundle.js). Проверяем через mump: все работает. Единственной, не будет работать отправка форм, при отправке ничего не произойдет и в консоли будет ошибка openModal is not defined, тк функция openModal находится в другом модуле. Пока оставляем так.

    // РЕШЕНИЕ ПРОБЛЕМЫ с openModal/closeModal

    // Для того, чтобы это исправить, сделаем экспорт конкретно этих двух функций из файла modal с помощью
    // export {closeModal};
    // export {openModal};
    // При этом эти функции из функции modal в фойле modal.js нужно вынести и поставить ПЕРЕД функцией modal (см. файл modal.js). Теперь идем в файл forms и импортируем там эти две функции с помощью import {openModal, closeModal} from './modal';
    // Теперь если зайдем на сайт и нажмем на кнопку, то выпадет ошибка
    // TypeError: Cannot read properties of undefined (reading 'add')
    // чтобы посмотреть откуда растут ноги, заходим на 2 строчку (справа от ошибки показывает где именно ошибку, туда надо нажать, и он перекинет). Дело в том, что переменной modal просто не существует внутри функции openModal, а мы пытаемся использовать classlist на том, чего не существует. Здесь нужна инкапсуляция. Каждый модуль должен быть независим друг от друга. Это не значит, что нельзя вызывать кучосек модуля в другом модуле, это значит, что они не должны привязываться к одним и тем же сущностям, как у нас получилось с переменной modal. И еще нужно помнить, что один и тот же модуль может быть вызван несколько раз для разных элементов. Это очень частая практика, тк таймеров и калькуляторов может быть несколько, на они могут использовать разные селекторы, элементы. Посмотрим на наш слайдер: мы здесь жестко забили какие-то селекторы, которые используются на странице. Но если мы захотим сделать еще один слайдер? Нам необходимо дублировать код. Либо есть другой выход: мы должны создавать модули так, чтобы они зависели только от аргументов, которые в них передаются. Ведь эти все селекторы могут быть переданы как аргументы функции slider, и уже потом на этапе вызова функции будут уже срабатывать на определенных элементах на странице. Сейчас этим и займемся. Углубиться в это все можно на курсе https://www.udemy.com/course/javascript_practice/?referralCode=9DD7B19DD5505880B724 (можно поискать на сливе), а сейчас нам надо понять, как это все работает.
    // В файле modal в функции прописываем параметры, которые передаем в функцию modal (см. в файле).
    // теперь если запустить, форма открывается, но выдает ошибку, что не найден modalTimerID в функции openModal. И


